* {
    margin: 0; padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style-type: none;
  }

.home .main_container {
    width: 100%;
    height: 100vh;
    background-color: #22262A;
}

.home .main_container .sub_container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  height: 100%;
  width: 100%;
  padding-right: 100px;
}
.home .main_container .sub_container .ment {
  width: 400px;
  margin-left: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.home .main_container .sub_container .ment h2{
  font-size: 45px;
  color: white;
  font-family: Urbanist;
  font-weight: bold;
  height: 220px;
  width: 400px;
}

/* 버튼 반짝이 효과 애니메이션 */
.home .main_container .sub_container .ment button {
  margin-top: 50px;
  width: 250px;
  height: 60px;
  font-family: Urbanist;
  font-weight: bold;
  font-size: 17px;
  color: white;
  font-weight: bold;
  border-radius: 50px;
  background-color: transparent;
  border: 4px solid #61DAFB;
	overflow: hidden;
	position: relative;
  display: inline-block;
  cursor: pointer;
}

.home .main_container .sub_container .ment button {
	z-index: 2;
}

.home .main_container .sub_container .ment button:after {
	content: "";
	height: 155px;
	left: -75px;
	opacity: .2;
	position: absolute;
	top: -50px;
	background: #fff;
	-webkit-transform: rotate(35deg);
	transform: rotate(35deg);
	-webkit-transition: all .55s cubic-bezier(0.19, 1, 0.22, 1);
	transition: all .55s cubic-bezier(0.19, 1, 0.22, 1);
	width: 50px;
	z-index: -1;
}

.home .main_container .sub_container .ment button:hover:after {
	left: 120%;
}


/* 리액트 로고 돌아가는 애니메이션 */
.home .main_container .sub_container .react_logo {
  display: flex;
  align-items: center;
  justify-content: center;
}

